.new-product {
  display: flex;
  align-items: center;
  flex-direction: column;
  padding: 1.5rem 0;
  text-transform: uppercase;
  color: var(--header);
  transition: all 0.3s ease-in-out;

  p:first-of-type {
    font-weight: 600;
  }

  &:hover {
    cursor: pointer;
    color: var(--text-header);
    background-color: var(--header);
    transform: scale(0.98);
  }

  &__img {
    //border: 1px solid green;
    margin: 1rem 0;
    width: 7rem;
    height: 7rem;
  }

  img {
    height: auto;
    width: 100%;
    object-fit: cover;
    transition: all 0.5s ease-in-out;
  }
}
